import { LightLoading } from './../../../light/light-providers/providers/lightLoading';
import { LightHttp, LightJsonUtil } from './../../../light/light-out/light-out.module';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-notice-detail',
  templateUrl: 'notice-detail.html',
})
export class NoticeDetailPage {

  code: any;
  //通知标题
  noticeTitle: any = {};
  //通知内容
  Content: any;
  // 附件
  attachList: Array<any>;

  /**
   * 
   * @param {NavController} navCtrl 
   * @param {NavParams} navParams 
   * @param {LightHttp} lightHttp 
   * @memberof NoticeDetailPage
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightHttp: LightHttp,
    public lightLoading: LightLoading
  ) {
    this.code = navParams.data.item.CODE;
  }

  public ngOnInit(): void {
    this.getNoticeDetail();
  }

  /**
   *获取通知标题、内容、附件
   *
   * @memberof NoticeDetailPage
   */
  async getNoticeDetail() {
    // 启用等待的遮罩层
    this.lightLoading.presentLoading('', () => {
      console.log('new detail loading ok')
    })
    // 同时发起请求
    const [newTitle, Content, attachList] = await Promise.all([
      this.lightHttp.get("Notice/GetNoticeInfo", { objectID: this.code }),
      this.lightHttp.get("Notice/GetNoticeContent", { objectID: this.code }),
      this.lightHttp.get("Attachment/GetAttachments", { objectID: this.code }),
    ])
    // 等三个请求的数据全部返回后再渲染页面
    this.noticeTitle = LightJsonUtil.toObject(newTitle);
    this.Content = Content;
    this.attachList = LightJsonUtil.toObject(attachList) as Array<any>;
    // 关闭遮罩层
    this.lightLoading.stopLoading()
  }
}








